function isTeacher(roleIds) {
  console.log(': ', roleIds)
  if (!Array.isArray(roleIds)) {
    console.log("")
    return false
  }

  if (roleIds.indexOf('1') >= 0) {
    console.log('')
    return true
  }
  if (roleIds.indexOf(1) >= 0) {
    console.log('2')
    return true
  }
  console.log('')
  return false
}

function getInstrumentUrl(id) {
  let url = ""
  instruments.some(item => {
    if (item.id === id) {
      url = item.url
    }
  })
  return url
}

